><span style="font-family:宋体; font-size:14pt">平台的服务。</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">受合作商家的委托，将商家的让利部分按</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">平台的奖励规则对您及其它关联方进行奖励。双方根据有关法律法规规定，本着互惠、互利共同发展、公平合理的原则达成如下协议，以示遵守。</span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">2、注册、账户和实名认证</span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">2.1在您按照注册页面提示填写信息、阅读并同意本服务协议且完成全部注册程序后，您即已获得</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">账户，正式成为</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">的用户。您可以使用您提供或确认的手机号码或者我方允许的其它方式作为登录手段进入本平台。</span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">2.2您确认，在您完成注册程序或以其它我方允许的方式实际使用本服务时，您应当是具备完全民事权利能力和完全民事行为能力的自然人。</span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">2.3若您是未成年人或限制民事行为能力人，则您不具备前述主体资格，您及您的监护人应承担因您的不当注册行为而导致的一切后果，且我方有权注销(永久冻结)您的账户，并向您及您的监护人索赔。</span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">2.4在完成注册或激活流程时，按相应页面的提示准确提供并及时更新您的资料，确保真实、完整和准确。若</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">有合理理由怀疑您提供的资料错误、不实、过时或不完整的，我方有权向您发出询问/或要求改正的通知，并有权直接做出删除相应资料的处理，或中止、终止对您提供部分或全部服务。我方对此不承担任何责任，您将承担因此产生的任何直接或间接损失及不利后果。</span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">2.5若因您提供的注册信息不准确或过时，包括您提供的电子邮件地址、联系电话、联系地址、等联系方式，造成我方无法与您取得联系，导致您在使用</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">服务过程中产生任何损失或增加费用的，应由您完全独自承担。您了解并同意，您有义务保持您提供的联系方式的有效性，如有变更需要更新的，您应按</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">的要求进行操作修改。</span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">2.6目前</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">允许一个法律主体拥有一个</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">账户，但一个</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">账户仅能对应唯一的法律主体。除非有法律规定或生效法律文书确定，或者符合我方公布的条件，否则您不得以任何方式转让、赠与或让他人继承您的</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">账户。同时，在进行符合条件的</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">账户转让、赠与或继承时，我方有权要求您、及/或受让受赠者、或您的继承人提供合格的文件材料并按照我方要求的操作流程办理</span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">2.7您了解并同意，为保证账户和交易安全，我方有权随时要求您完成</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">账户的实名认证；同时，</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">可能会就某些产品或服务的开通，要求您提供更多的身份资料和信息，做进一步的身份认证或资格验证您的账户只有在通过这些认证和验证之后，方可获得使用相关产品或服务的资格。</span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">2.8通常情况下，您的</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">账户是您我方进行一切活动的唯一身份识别依据，每一个</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">账户都可以在本APP独立开展活动。但在下列情形下，我方有权根据自己的判断，对同一及/或关联法律主体拥有的多个</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">账户进行统一处理。</span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">3、账户安全</span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">3.1您须自行负责对您的</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">账户和密码保密，且须对您在该登录名和密码下发生的所有活动(包括但不限于信息披露、发布信息、网上点击同意或提交各类规则协议、续签协议或购买服务等)承担责任。您同意：(a)如发现任何人未经授权使用您的</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">账户和密码，或发生违反保密规定的任何其他情况，您会立即通知</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">；(b)确保您在每个上网时段结束时，以正确步骤离开平台。</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">不能也不会对因您未能遵守本款规定而发生的任何损失或损毁负责。您理解</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">对您的请求采取行动需要合理时间，</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">对采取行动前已经产生的后果(包括但不限于您的任何损失)不承担任何责任。</span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">3.2除非有法律规定或司法裁定，且征得</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">的同意，否则，您的</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">登录和密码不得以任何方式转让、赠与或继承(与账户相关的财产权益除外)。</span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">3.3您理解并同意，我方有权了解您使用我方产品及服务的真实背景及目的，有权要求您如实提供真实、全面、准确的信息；如果我方有合理理由怀疑您提供的信息不真实、您进行虚假交易，或您的行为违反</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">规则的，</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">有权暂时或永久限制您账户下所使用的所有产品/或服务的部分或全部功能。</span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">3.4您理解并同意，基于运行和交易安全的需要，</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">有权暂时停止或者限制您账号下部分或全部</span><span style="font-family:宋体; font-size:14pt">星币</span><span style="font-family:宋体; font-size:14pt">的申请回购或转赠功能，</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">将通过邮件、信息、短信或电话等方式通知您，您应及时予以关注并按照程序进行申诉等后续操作。</span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">3.5您理解并同意，</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">有权按照国家司法、行政、军事、安全等机关(包括但不限于公安机关、检察机关、法院、海关、税务机关、安全部门等)的要求对您的个人信息及个人账户交易信息等进行查询、冻结。</span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">3</span><span style="font-family:宋体; font-size:14pt">.</span><span style="font-family:宋体; font-size:14pt">6保密协议</span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">1)您同意在合作过程中涉及的所有秘密信息，任何一方未经商业秘密提供方同意，均不得将该信息向任何第三方披露、传播、编辑或展示。</span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">2)因对方书面同意以及国家行政、司法强制行为而披露商业秘密的，披露方不承担责任；该商业秘密已为公众所知悉的，披露方不承担责任。</span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">3.7账户注销</span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">3.7.1</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">保留在您违反国家、地方法律法规规定或违反本服务协议的情况下，中止或终止为您提供部分或全部服务、直至注销</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">账户的权利。</span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">3.7.2您理解并同意，如您连续12个月未使用您的手机或我方认可的其他方式和密码登录，且您的账户下不存在任何未到期的服务，</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">有权注销您的登录名，您将不能再登录</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">平台。</span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">3.7.3您同意并授权</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">，如您在</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">平台有欺诈、发布或销售伪劣商品、侵犯他人合法权益或其他严重违反任</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">规则的行为，您的</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">登录名可能被注销，被注销后您不能再登录</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">平台，</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">服务同时终止。</span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">3.7.4您保证不会采取任何影响、损害或可能影响损害</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">及其关联公司合法权益，前述影响或损害</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">及关联公司合