ndent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">7.3.2国家有权机关要求进行冻结的</span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">7.3.3您使用</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">服务的行为涉嫌违反国家法律法规及行政规定的</span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">7.3.4我方基于单方面合理判断，认为您的账户操作资金进出等存在异常时；或认为可能产生风险的其他情况。</span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">7.3.5您遭到他人投诉，且对方已经提供了一定证据的</span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">7.3.6您在合作商家的消费数据是假消费，并没有产生真实的消费时。我方按照前述规定对您的账户进行冻结后，将通过邮件、站内信息、短信或电话等方式通知您按照相应的程序进行申诉。如果您按照本服务协议的要求进行申诉并经我方审核通过的，将立即解除账户冻结措施；如果您未在规定时间内进行申请，或您的申诉未能通过我方审核的，则您确认我方有权长期冻结该账户。</span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">8、隐私及其他个人信息的保护</span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">一旦您同意本服务协议或使用本服务，您即同意我方按照以下协议来使用和披露您的个人信息。</span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">8.1登录名和密码</span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">在您注册帐户时，</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">会要求您设置</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">账户登录名和密码来识别您的身份。您仅可通过您设置的密码来使用该账户，如果您泄漏了密码，您可能会丢失您的个人识别信息，并可能导致对您不利的法律后果。该账户和密码因任何原因受到潜在或现实危险时，您应该立即和我方取得联系，在我方采取行动前所造成的损失我方对此不负任何责任。</span></