span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">8.7您进一步同意</span><span style="font-family:宋体; font-size:14pt">趣分利</span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">8.7.1向您发送定制化的信息或新产品/服务信息，通过电邮、电话(包括语音电话、传真或信息，如果您表明我们可以以此方式联系您)</span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">8.7.2出于为您提供增强或分析的统计功能使用您的个人信息，或者出于销售或鼓励的目的使用或披露个人信息</span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">9、系统中断或故障</span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">系统可能因下列状况无法正常运作，使您无法使用各项互联网服务时，我方不承担损害赔偿责任，该状况包括但不限于：</span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">9.1我方系统停机维护期间。</span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">9.2电信设备出现故障不能进行数据传输的。</span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">9.3因合风、地震、海啸、洪水、停电、战争、恐怖袭击等不可抗力之因素，造成</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">系统障碍不能执行业务的。</span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">9.4由于黑客攻击、电信部门技术调整或故障、升级银行方面的问题等原因而造成的服务中断或者延迟。</span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">10、责任范围及责任限制：</span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">10.1