e="font-family:宋体; font-size:14pt">.</span><span style="font-family:宋体; font-size:14pt">3甲乙双方同意，与本协议有关的任何通知，以书面方式送达方为有效。书面形式包括但不限于：快递、邮件、电子邮件。上述通知应被视为在下列时间送达以电子邮件形式发出的，自电子邮件进入对方邮件系统之时，视为通知到达；以快递/书面邮件形式发出的自对方签收之日或快递发出之日起5日内视为通知到达。</span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">14、本服务协议相关文字的定义</span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">14.1</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">平台，是指</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">和APP系统及其它关联部分(以下简称平台)</span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">14</span><span style="font-family:宋体; font-size:14pt">.</span><span style="font-family:宋体; font-size:14pt">2奖励：是指商家的让利资金委托</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">平台对消费会员及关联方进行分配。</span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">14.3代理商：是指从</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">获得权限，代表</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">和商家洽谈、签约，后续对商家监管、服务的自然人或公司。</span></p><p style="margin:0pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:宋体; font-size:14pt">14</span><span style="font-family:宋体; font-size:14pt">.</span><span style="font-family:宋体; font-size:14pt">4</span><span style="font-family:宋体; font-size:14pt">趣分利</span><span style="font-family:宋体; font-size:14pt">对本协议拥有最终解释权。</span></p></div><div class="cnzz" style="display: none;">`
